<template lang="pug">
.cus-dropdown-wrapper
  b-button.cus-dropdown-btn(
    @blur="show('blur')"
    @click="show('click')") {{ valueInner ? '全部' : '未讀' }}
    .cus-dropdown-menu(:class="{'show': isShow}")
      .cus-dropdown-item(@click="update(true)") 全部
      .cus-dropdown-item(@click="update(false)") 未讀
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      isShow: false,
    };
  },
  computed: {
    valueInner: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    update(value) {
      this.valueInner = value;
    },
    show(e) {
      e === 'blur'
        ? this.isShow = false
        : this.isShow = !this.isShow;
    },
  },
};
</script>

<style lang="scss">
.cus-dropdown-wrapper {
    position: relative;
    display: inline-flex;
    vertical-align: middle;
  .cus-dropdown-btn {
    display: flex;
    align-items: center;
    font-size: 14px;
    background-color: #fff;
    color: #212529;
    border: none;
    padding: 4px 4px 4px 8px;
    white-space: nowrap;
  
    &:hover, &:active, &:focus {
      background-color: #fff !important;
      color: #212529 !important;
      box-shadow: none !important;
    }
    &::after {
      display: inline-block;
      margin-left: 6px;
      margin-top: 2px;
      vertical-align: 0.255em;
      content: "";
      border-top: 0.3em solid;
      border-right: 0.3em solid transparent;
      border-bottom: 0;
      border-left: 0.3em solid transparent;
    }
  }
  .cus-dropdown-menu {
    width: 80px;
    font-size: 14px;
    padding: 8px 0;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    font-size: 14px;
    color: #212529;
    text-align: center;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: 0.25rem;
    &.show {
      top: 24px;
      left: 6px;
      display: block;

      @media only screen and (max-width: 991px) {
        top: 30px;
        left: -30px;
      }
    }
    .cus-dropdown-item {
      padding: 2px 0;
      white-space: nowrap;
      cursor: pointer;
      &:active, &:hover {
        background-color: #e9ecef;
        color: #212529;
      }
    }
  }
}
</style>